  props: {
    //接收父组件传递过来的参数方法
    deta: {
      default: function() {
        return '';
      }
    }
  },
  beforeCreate(){
    //实例未创建，调用不到data数据

  },
  created() {
    //实例创建完成后调用,此阶段完成了数据的观测,可调用data数据
    //添加每个数组默认的坐标
    this.transferFun()
    if(this.$route.query.sm_class == undefined){
      this.activeClass = 0
    }else{
      this.activeClass = this.$route.query.sm_class
    }
  },
  
  methods: {
    reload () {
      this.isRouterAlive = false
      this.$nextTick(() => (this.isRouterAlive = true))
    },
    transferFun: function(){
      let cls = this.$route.query.sm_class
      if(!cls){
        this.transfer_article = [].concat(this.article)
      }else{
        let arr = [];
        for(let i = 0;i < this.article.length;i++){
          if(this.article[i].sm_class == cls){
            arr.push(this.article[i])
          }
        }
        this.transfer_article = [].concat(arr)
        //console.log(this.transfer_article)
      }
    },
    paging:function(a){
      if(this.at_num == 1){
        $('.prev').hide()
        if(this.sum_num>this.at_num){
          $('.present').show()
          $('.next').show()
          $('.sum').show()
        }else{
          $('.present').hide()
          $('.next').hide()
          $('.sum').hide()
        }
      }else{
        $('.prev').show()
        $('.sum').show()
        if(this.sum_num>this.at_num){
          $('.next').show()
        }else{
          $('.next').hide()
        }
      }
    }
  },
  mounted:function(){
    this.paging(this.at_num)
  },
  //监听
  watch: {
      $route(){
        this.clss = this.$route.query.sm_class
        if(this.$route.query.sm_class == undefined){
          this.activeClass = 0
        }else{
          this.activeClass = this.$route.query.sm_class
        }
      },
      clss() {
        this.reload()
        this.transferFun()
      }
  },
};
</script>

